* {
  border: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Inter', sans-serif; /* Using Inter font, with a common sans-serif fallback */
}

/* Basic body styling for a clean background */
body {
    background-color: #f8f8f8; /* A very light, subtle background */
}

/* Container for the entire featured product section */
.featured-product {
  padding: 50px 20px; /* Increased top/bottom padding for more vertical space */
  width: 100%;
  max-width: 1200px; /* Limits width on very large screens for better readability */
  margin: 0 auto; /* Centers the content horizontally */
  display: flex; /* Use flexbox for potential future horizontal layouts */
  flex-direction: column; /* Stack items vertically by default */
  align-items: center; /* Center items horizontally within this container */
}

/* Styling for the main heading */
.featured-product h1 {
    color: #c06200; /* Your brand color for emphasis */
    font-weight: 700; /* Bolder for prominence */
    font-size: 2.8rem; /* Slightly larger heading */
    margin-bottom: 30px; /* More space below the heading */
    text-align: center; /* Ensure heading is centered */
}

/* Container for the individual featured slide content */
.featured-slide {
    width: 100%;
    background-color: #ffffff; /* Clean white background for the card */
    border: 1px solid #e0e0e0; /* A very light border for definition */
    padding: 30px;
    border-radius: 16px; /* Slightly more rounded corners for a softer look */
    display: flex;
    flex-direction: column;
    align-items: center; /* Center content horizontally within the slide */
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.08); /* A softer, more pronounced shadow for depth */
    transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out; /* Smooth transitions for hover effects */

    /* Add a min-height or aspect-ratio to ensure it holds space for the image */
    min-height: 400px; /* Ensures vertical space even if image is not loaded or for responsiveness */
    position: relative; /* Essential for absolutely positioning elements *inside* it */
}

.featured-slide:hover {
    transform: translateY(-8px); /* Lifts the card more noticeably on hover */
    box-shadow: 0 12px 25px rgba(0, 0, 0, 0.15); /* Stronger shadow on hover */
}

/* Styling for the product image */
.featured-slide img {
    position: absolute; /* Takes image out of normal flow for layering/fade */
    top: 50%; /* Moves top edge to vertical center of parent */
    left: 50%; /* Moves left edge to horizontal center of parent */
    transform: translate(-50%, -50%); /* Centers the image perfectly based on its own size */
    width: 100%; /* Image fills its container up to its max-width */
    height: 100%; /* Image fills its container vertically */
    object-fit: cover; /* Ensures image covers its area nicely without distortion */
    border-radius: 12px; /* Rounded corners for the image */
    opacity: 0; /* Hidden by default for slideshow fade effect */
    transition: opacity 1s ease-in-out; /* Smooth fade transition */
}

/* Active image styling for slideshow */
.featured-slide img.active {
    opacity: 1; /* Only active image is visible */
}

/* Styling for the product name */
.featured-slide h3 {
    margin-bottom: 12px;
    font-size: 2.5rem; /* Slightly larger product name */
    color: #333; /* Darker text for good contrast */
    font-weight: 600; /* Medium bold for readability */
    text-align: center; /* Center product name */
    position: relative; /* Ensures text is on top of absolute image */
    z-index: 1; /* Ensures text is above image */
}

/* Styling for the product description */
.featured-slide p {
    margin-bottom: 30px;
    font-size: 1.5rem; /* Comfortable reading size */
    color: #666; /* Softer grey for body text */
    font-weight: 300; /* Lighter weight for readability */
    line-height: 1.6; /* Improved line spacing for readability */
    text-align: center; /* Center description */
    max-width: 600px; /* Limit width for better text flow */
    position: relative; /* Ensures text is on top of absolute image */
    z-index: 1; /* Ensures text is above image */
}

/* Styling for the call-to-action button */
.featured-slide button {
    padding: 18px 35px; /* More generous padding for a larger button */
    background-color: #c06200; /* Your brand color */
    border: none;
    border-radius: 10px; /* Slightly more rounded button */
    color: #fff;
    cursor: pointer;
    font-size: 1.3rem; /* Larger font size for button text */
    font-weight: 600;
    letter-spacing: 0.5px; /* Slight letter spacing for better appearance */
    transition: background-color 0.3s ease, transform 0.2s ease; /* Smooth hover and click effects */
    text-transform: uppercase; /* Make button text uppercase */
    position: relative; /* Ensures button is on top of absolute image */
    z-index: 1; /* Ensures button is above image */
}

.featured-slide button:hover {
    background-color: #a35300; /* Darken on hover */
    transform: translateY(-2px); /* Slight lift on hover */
}

.featured-slide button:active {
    transform: translateY(0); /* Press down effect on click */
}

/* Container for the navigation dots */
.dot-container {
  text-align: center;
  margin-top: 40px; /* More space above dots */
  display: flex;
  justify-content: center; /* Center dots using flexbox */
  gap: 10px; /* Space between dots */
  position: relative; /* Ensures dots are on top of absolute image */
  z-index: 1; /* Ensures dots are above image */
}

/* Individual navigation dot styling */
.dot {
  cursor: pointer;
  height: 14px; /* Slightly smaller dots */
  width: 14px;
  background-color: #ddd; /* Lighter default dot color */
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.4s ease, transform 0.2s ease; /* Smooth transitions */
}

.dot:hover {
  background-color: #a35300; /* Match hover color to button */
  transform: scale(1.1); /* Slight enlarge on hover */
}

/* Active dot styling */
.dot.active {
  background-color: #c06200; /* Your brand color for the active dot */
  transform: scale(1.1); /* Keep active dot slightly larger */
}

/* --- Mobile Styles (max-width: 767px) --- */
@media screen and (max-width: 767px) {
    .featured-product {
        padding: 30px 15px; /* Adjusted padding for smaller screens */
    }

    .featured-product h1 {
        font-size: 2rem;
        margin-bottom: 20px;
    }

    .featured-slide {
        padding: 20px;
        border-radius: 12px; /* Keep rounded corners consistent */
        box-shadow: 0 4px 10px rgba(0, 0, 0, 0.08); /* Lighter shadow on mobile */
        min-height: 300px; /* Adjust min-height for mobile */
    }

    .featured-slide img {
        margin-bottom: 15px;
        border-radius: 8px; /* Slightly less rounded image on mobile */
    }

    .featured-slide h3 {
        font-size: 1.8rem; /* Smaller heading on mobile */
        margin-bottom: 8px;
    }

    .featured-slide p {
        font-size: 1.1rem; /* Smaller description text on mobile */
        margin-bottom: 20px;
    }

    .featured-slide button {
        padding: 12px 25px;
        font-size: 1rem;
        width: 100%; /* Full width button for better tap target on mobile */
        max-width: 300px; /* Prevent button from becoming too wide on tablets */
    }

    .dot-container {
      margin-top: 30px;
      gap: 8px; /* Slightly less space between dots on mobile */
    }

    .dot {
      height: 12px;
      width: 12px;
    }
}